%border {
	border: 2px solid $primary-border-color;
}

%horizontalRule {
	border: 1px solid $primary-border-color;
	margin: 0;
	margin-bottom: 10px;
	clear: both;
	height: 0;
	width: 98%;
}

%templateCard {
	color: $primary-font-color;
	background-color: $secondary-bg-color;
	margin: 20px;
	@extend %border;
}

%centerFlex {
	display: flex;
	justify-content: center;
	align-items: center;
}

%transitionSlow {
	transition: all 0.5s;
	-webkit-transition: all 0.5s;
}

%transitionFast {
	transition: all 0.3s;
	-webkit-transition: all 0.3s;
}

@mixin cardDimensions($value) {
	height: $value;
	width: $value;
}
