@import "./../../styles/partials/variables";
@import "./../../styles/partials/mixins";

.MenuIcon {
	cursor: pointer;
	color: $primary-font-color;
	position: fixed;
	z-index: 20;
	font-size: 1.5em;
	left: 0.5em;
	top: 0.5em;
}

.Navbar {
	@extend %transitionSlow;
	z-index: 10;
	position: absolute;
	height: 3rem;
	width: 100vw;
	display: flex;
	color: $primary-font-color;
	justify-content: space-evenly;
	align-items: center;
	background-color: $primary-bg-color;
	margin: 0;
	padding: 0;
	list-style: none;
}

.NavbarVertical {
	@extend %transitionSlow;
	color: $primary-font-color;
	z-index: 10;
	position: fixed;
	left: -120%;
	height: 100%;
	width: 100vw;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	background-color: transparent;
	margin: 0;
	padding: 0;
	padding-top: $vertnav-height;
	list-style: none;
	&_Bar {
		z-index: 20;
		height: $vertnav-height;
		width: 100%;
		position: fixed;
		background-color: $primary-vertnav-color;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&_PageTitle {
		color: $primary-font-color;
		font-size: 1.3em;
		font-weight: lighter;
		margin: 0;
	}
	&__Expanded {
		@extend .NavbarVertical;
		background-color: $primary-vertnav-color;
		left: 0;
	}
}
