$xsmax: 480px; //extra small
$smmin: 481px; //small
$mdmin: 769px; //medium
$lgmin: 1025px; //large
$xlmin: 1201px; //extra large

@mixin xsmall {
	@media only screen and (max-width: $xsmax) {
		@content;
	}
}
@mixin small {
	@media only screen and (min-width: $smmin) {
		@content;
	}
}
@mixin medium {
	@media only screen and (min-width: $mdmin) {
		@content;
	}
}
@mixin large {
	@media only screen and (min-width: $lgmin) {
		@content;
	}
}
@mixin xlarge {
	@media only screen and (min-width: $xlmin) {
		@content;
	}
}
