@import "./../../styles/partials/variables";
@import "./../../styles/partials/mixins";
@use "./../../styles/partials/devices.scss";

.Home {
	@extend %centerFlex;
	@extend %transitionFast;
	color: $primary-font-color;
	opacity: 1;
	height: 100%;
	flex-direction: column;
	&_Title {
		margin: 0;
		text-shadow: 3px 2px 3px $primary-highlight-color,
			0px 3px 10px $primary-border-color;
		@include xsmall {
			font-size: 6vh;
		}
		@include small {
			font-size: 7vh;
		}
		@include medium {
			font-size: 9vh;
		}
	}
	&_Subtitle {
		@extend .Home_Title;
		@include xsmall {
			font-size: 4vh;
		}
		@include small {
			font-size: 5vh;
		}
		@include medium {
			font-size: 7vh;
		}
	}
	&__Invis {
		@extend .Home;
		opacity: 0;
	}
}
