@import "./../../styles/partials/variables";

.DisplayWindow {
	background-color: $base-bg-color;
	height: 100vh;
	&__Vertical {
		@extend .DisplayWindow;
		display: flex;
		flex-direction: column;
		align-items: center;
		overflow: scroll;
		scroll-snap-type: y proximity;
		scroll-padding-top: 3.5em;
	}
}
