@import "./../../styles/partials/variables";
@import "./../../styles/partials/mixins";
@use "./../../styles/partials/devices.scss";

.ContentWrapper {
	@extend %templateCard;
	grid-area: info;
	margin-left: 0;
	overflow-y: scroll;
	text-align: left;
	padding: 10px;
	padding-top: 0;
	padding-left: 20px;
	&::-webkit-scrollbar {
		display: none;
	}
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
	&__Vertical {
		@extend .ContentWrapper;
		overflow-y: visible;
		padding: 20px;
		border-radius: 20px;
		scroll-snap-align: start;
		margin: 3vh;
		@include xsmall {
			margin: 3vh 0.5vh;
		}
	}
}

.ContentCard {
	@extend %transitionFast;
	height: 100%;
	display: flex;
	flex-direction: column;
	&_Title {
		text-align: center;
		font-weight: lighter;
	}
	&_ContentBox {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		& p {
			margin: 10px;
		}
	}
	&_ProjectsBox {
		width: 100%;
		display: flex;
		flex-direction: column;
	}
	&_HorizontalRule {
		@extend %horizontalRule;
	}
	&__Invis {
		@extend .ContentCard;
		opacity: 0;
	}
}

.ContentCardVertical {
	@extend %transitionFast;
	display: flex;
	flex-direction: column;
	align-items: center;
	&_Title {
		text-align: center;
		font-weight: lighter;
		margin: 10px;
		@include xsmall {
			font-size: 1em;
			margin: 5px;
		}
	}
	&_Icons {
		font-size: 1.5em;
		margin: 10px;
		@include xsmall {
			font-size: 1em;
			margin: 5px;
		}
		& svg {
			padding: 0 10px;
		}
	}
	&_ContentBox {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		& p {
			margin: 10px;
			@include xsmall {
				margin: 10px -5px;
			}
		}
	}
	&_ProjectsBox {
		width: 100%;
		display: flex;
		flex-direction: column;
	}
	&_HorizontalRule {
		@extend %horizontalRule;
	}
	&__Invis {
		@extend .ContentCardVertical;
		opacity: 0;
	}
}

.VerticalSpacer {
	height: $vertnav-height;
}
